*,* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box
}

html {
  position: relative;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  border: 0;
}

nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 3.2em;
  width: 100%;
  background-color: #2C2B2A;
  padding: 1rem;
  font-family: 'Effra','-apple-system', 'BlinkMacSystemFont','Segoe UI','Roboto','Helvetica','Arial','sans-serif','Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol','sans-serif';
  color: #F5EDED;
  z-index: 10000;
}

.c-hr--gradient {
  width: 100%;
  height: 20px;
  background-image: -webkit-gradient(linear, right top, left top, from(#862984), to(#E64257));
  background-image: -webkit-linear-gradient(right, #862984 0%, #E64257 100%);
  background-image: -o-linear-gradient(right, #862984 0%, #E64257 100%);
  background-image: linear-gradient(-90deg, #862984 0%, #E64257 100%);
}

small {
  font-family: 'Effra','-apple-system', 'BlinkMacSystemFont','Segoe UI','Roboto','Helvetica','Arial','sans-serif','Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol','sans-serif';
}

.c-nav-time {
  opacity: .75;
  text-align: right;
  width: 100%;
  float:right;
  font-size: .75rem;
}

.c-nav-time__label {
  text-transform: uppercase;
  letter-spacing:.1em;
  font-weight: bold;
}

.c-nav__time time {
  font-variant-numeric: tabular-nums;
}

main {
  margin-top: 3rem;
}

form {
  font-family: 'Effra','-apple-system', 'BlinkMacSystemFont','Segoe UI','Roboto','Helvetica','Arial','sans-serif','Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol','sans-serif';
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.w-signature {
  position: -webkit-sticky;
  position: sticky;
  top: 3rem;
}

.l-sticky {
  position: relative;
}

fieldset {
  border-radius: .25rem;
  border: 1px solid #DBDBDB;
  background-color: #F7F7F7;
  padding-top: .75rem;
}

fieldset + fieldset {
  margin-top: 1.5rem;
}

.c-form-input__example {
  color: #a5a3a0;
  font-size: .75em;
  margin-left: .25rem;
}

.c-form-input__example + input {
  margin-top: .35rem;
}

.c-form-label__optional {
  font-size: .75em;
  color: #7C7A7A;
  letter-spacing: .2;
  margin-left: .25rem;
}

label {
  color: #4a4a4a;
  font-size: .85rem;
  margin-left: .25rem;
}

input {
  margin-top: .4rem;
  margin-bottom: 1rem;
  width: 100%;
  max-width: 27.75rem;
  padding: .45rem 0 .45rem .45rem;
  margin-right: .45rem;
  border-radius: .5rem;
  font-size: .75em;
  border: 1px solid #EAEAEA;
}

input:focus {
  border-radius: .5rem;
}

input:focus-within {
  border-radius: .5rem;
}

input[type=url], input[type=photo] {
  font-family: 'Operator Mono', 'Fira Code Retina', Menlo, Monaco, 'Courier New', monospace;
}

legend {
  text-transform: uppercase;
  letter-spacing: .6;
  font-size: .9em;
}

legend + label {
  margin-top: 1.5rem;
}

footer {
  color: #979797;
}

footer a {
  background-image: -webkit-gradient(-90deg, #862984 0%, #E64257 100%);
  background-image: -webkit-linear-gradient(-90deg, #862984 0%, #E64257 100%);
  background-image: -o-linear-gradient(-90deg, #862984 0%, #E64257 100%);
  background-image: -webkit-gradient(linear, right top, left top, from(#862984), to(#E64257));
  background-image: -webkit-linear-gradient(right, #862984 0%, #E64257 100%);
  background-image: -o-linear-gradient(right, #862984 0%, #E64257 100%);
  background-image: linear-gradient(-90deg, #862984 0%, #E64257 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#862984', endColorstr='#E64257', GradientType=1 );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.u-screen-reader {
    position: fixed !important;
    _position: absolute !important;
    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px);
}

.u-mb7 {
  margin-bottom: 5rem;
}


